body {
    color: white;
}

span,
label {
    line-height: 35px;
}

option {
    outline: none;
}

/* productlist/list */
.product {
    width: 75%;
    background: #3B3838;
    margin: 0 auto;
}

.list-product-frame {
    display: flex;
    position: relative;
    color: white;
    border: 2px solid red;
}

.list-product {
    color: white;
    width: 90%;
    margin: 10px auto;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
    overflow-y: auto;
}

.list-product::-webkit-scrollbar {
    height: 5px;
    width: 2px;
    border: 1px solid rgb(208, 57, 57);
    background: rgb(252, 252, 252);
}

.list-product::-webkit-scrollbar-track {

    -webkit-box-shadow: inset 0 0 6px rgb(0, 0, 0, 0.3);
}

.list-product::-webkit-scrollbar-thumb {
    background-color: yellow;
    outline: 1px solid rgb(26, 137, 47);
}

/* productlist/option */

.price-option {
    width: 10%;
    height: 10%;
    padding: 1% 10px 1% 10px;
    border-radius: 10px;
    outline: none;
    border: none;
}

.filter-button {
    width: 5%;
    height: 10%;
    padding: 1% 5px 1% 5px;
    border-radius: 10px;
    background-color: #0070C0;
    outline: none;
    border: none;
    color: white;
}

.order-option {
    width: 70%;
    margin: 2% auto;
    display: flex;
    justify-content: space-evenly;
}

.select {
    padding: 1%;
    border-radius: 10px;
    outline: none;
}

/* productitem/value */
.product-info {
    width: 75%;
    margin: 5% auto;
    font-size: 30px;
}

.product-view {
    position: relative;
    display: flex;
    padding-bottom: 40px;
    border-bottom: 2px solid #CA1831;
}

.product-view-sell {
    position: absolute;
    right: 15%;
    width: 30%;
}

.product-view span button {
    color: white;
    width: 40%;
    padding: 5% 2%;
    margin-right: 10%;
    border-radius: 8px;
    outline: none;
    border: none;
    cursor: pointer;
}

.product-view-client textarea {
    width: 80%;
    padding: 15px 10px 20px;
    outline: none;
    border-radius: 20px;
}

.product-view-client button {
    background-color: #0070C0;
    color: white;
    width: 10%;
    height: 10%;
    padding: 1% 2%;
    margin: 1% 0 0 4%;
    border-radius: 8px;
    outline: none;
    border: none;
    cursor: pointer;
}