@font-face {
    font-family: rubik;
    src: url("../../public/rubik.ttf");
}



/* header */
h1 {
    font-family: rubik;
    color: #CA1831;
}

.header {
    display: flex;
    background-color: black;
    height: 100px;
    width: 100%;
}

.search {
    display: flex;
    width: 30%;
    margin: 35px 0px 0px 15%;
}

.input-style {
    background-color: #E0D9FB;
    color: Red;
    padding: 10px;
    font-family: Arial;
    width: 80%;
    height: 15px;
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
    border: none;
    outline: none;
}

.info-group {
    display: flex;
    margin-left: 15%;
}

.info-header {
    font-size: 12px;
    display: flex;
    border: 2px solid white;
    border-radius: 10px;
    color: white;
    width: 90px;
    height: 45%;
    margin: 30px 0px 0px 50px;
    cursor: pointer;
}

/* main/category */
.category {
    width: 25%;
    background: #3B3838;
    margin-right: 2%;
    color: white;
}

.category-child {
    margin: 20px 0px 0px 20px;
}

.input-category {
    width: 80%;
    border-radius: 5px;
    outline: none;
    padding: 5px;
    font-family: Arial;
    border: none;
    margin-top: 15px;
}

li {
    cursor: pointer;
}


/* main/bestseller */
.best-seller {
    width: 73%;
    background: #3B3838;
}

.slide-bestseller-frame {
    display: flex;
    position: relative;
    color: white;
}

.slide-bestseller {
    color: white;
    width: 90%;
    margin: 10px auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    overflow-x: auto;
}

.slide-bestseller::-webkit-scrollbar {
    height: 5px;
    width: 2px;
    border: 1px solid rgb(208, 57, 57);
    background: rgb(252, 252, 252);
}

.slide-bestseller::-webkit-scrollbar-track {

    -webkit-box-shadow: inset 0 0 6px rgb(0, 0, 0, 0.3);
}

.slide-bestseller::-webkit-scrollbar-thumb {
    background-color: yellow;
    outline: 1px solid rgb(26, 137, 47);
}

/* main/sales */
.sales {
    width: 100%;
    background: #3B3838;
    margin-top: 2%;
}

.slide-sales-frame {
    display: flex;
    position: relative;
    color: white;
}

.slide-sales {
    color: white;
    width: 90%;
    margin: 10px auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    overflow-x: auto;
}

.slide-sales::-webkit-scrollbar {
    height: 5px;
    width: 2px;
    border: 1px solid rgb(208, 57, 57);
    background: rgb(252, 252, 252);
}

.slide-sales::-webkit-scrollbar-track {

    -webkit-box-shadow: inset 0 0 6px rgb(0, 0, 0, 0.3);
}

.slide-sales::-webkit-scrollbar-thumb {
    background-color: yellow;
    outline: 1px solid rgb(26, 137, 47);
}

/* LogPopup */
.cover-popup {
    display: none;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    z-index: 1;
    top: 0;
    margin: 0;
}

.window-popup {
    width: 50%;
    background-color: #E0D9FB;
    margin: 10% auto;
    padding: 1% 0 1% 3%;
}

.info-group-popup {
    width: 40%;
    display: flex;
    flex-direction: column;
    margin: 1% 5% 2% 0;
}

.input-log {
    font-family: dosis;
    font-size: 15px;
    height: 20px;
    border: none;
    outline: none;
    padding: 15px 10px;
    margin-top: 4%;
    border-radius: 7px;
}

.sign-up-popup {
    font-family: dosis;
    color: white;
    font-size: 15px;
    background-color: #0070C0;
    border-radius: 10px;
    padding: 4% 5% 4% 5%;
    margin-top: 5%;
    border: none;
    cursor: pointer;
}

.sign-in-popup {
    font-family: dosis;
    color: white;
    font-size: 15px;
    background-color: #0070C0;
    border-radius: 10px;
    padding: 4% 5% 4% 5%;
    border: none;
    cursor: pointer;
}

/* LogAd */
.log-ad-group {
    color: white;
    background-color: black;
}

.log-ad-group-child {
    display: flex;
    width: 70%;
    margin: 0 auto;
    padding: 20px;
}

.sign-up-ad {
    color: white;
    font-size: 15px;
    font-weight: 600;
    background-color: #2579f2;
    border-radius: 5px;
    height: 40px;
    padding-left: 13px;
    padding-right: 13px;
    margin-right: 20px;
    border: none;
    cursor: pointer;
}

.sign-in-ad {
    color: white;
    font-size: 15px;
    font-weight: 600;
    background: #000000;
    border: none;
    cursor: pointer;
}

/* Footer */
.footer {
    width: 100%;
    background: #3B3838;
}

.footer-child {
    display: flex;
    margin: 0 auto;
    width: 80%;
    justify-content: space-between;
}

.footer-info {
    display: flex;
    flex-direction: column;
    color: white;
    margin-bottom: 20px;
}

.footer-info span {
    cursor: pointer;
}

/**/
.checked {
    color: orange;
}